import { Router } from '@angular/router';
import { FlashMessagesService } from 'angular2-flash-messages';
import { ValidateService } from './../../services/validate.service';
import { Component, OnInit } from '@angular/core';
import { AuthService } from '../../services/auth.service';

@Component({
  selector: 'app-register',
  templateUrl: './register.component.html',
  styleUrls: ['./register.component.css']
})
export class RegisterComponent implements OnInit {
  constructor(
    private validateService: ValidateService,
    private _flashMessagesService: FlashMessagesService,
    private authService: AuthService,
    private _router: Router
  ) {}
  name: String;
  username: String;
  email: String;
  password: String;

  onRegisterSubmit() {
    const user = {
      name: this.name,
      email: this.email,
      username: this.username,
      password: this.password
    };

    // requredFields
    if (!this.validateService.validateRegister(user)) {
      this._flashMessagesService.show('please fill all field', {
        cssClass: 'alert-danger',
        timeout: 3000
      });
      return false;
    }

    // check email
    if (!this.validateService.validateEmail(user.email)) {
      this._flashMessagesService.show('Insert Valid Email', {
        cssClass: 'alert-danger',
        timeout: 3000
      });
      return false;
    }

    //Register User
    this.authService.registerUser(user).subscribe(data => {
      if (data.success) {
        this._flashMessagesService.show('Your Now Register please lognin', {
          cssClass: 'alert-success',
          timeout: 3000
        });
        this._router.navigate(['/login']);
      } else {
        this._flashMessagesService.show('Something Went Wrong', {
          cssClass: 'alert-danger',
          timeout: 3000
        });
        this._router.navigate(['/register']);
      }
    });
  }

  ngOnInit() {}
}



